@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Ubuntu:wght@300;400;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Ubuntu', sans-serif;
}

.active {
    font-weight: 700;
}

.hide {
    display: none;
}

.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    max-width: 500px;
}

/* FORM  CREATE & ADD*/
.form {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
}

.form__input {
    width: 100%;
    height: 40px;
    padding: 10px;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #ffe2a0;
    outline: none;
    font-size: 16px;
    font-family: inherit;
}
.form__input--name {
    font-size: 20px;
}

.form__input::-webkit-input-placeholder {
    font-style: italic;
    color: #999999;
}

.form__input:-ms-input-placeholder {
    font-style: italic;
    color: #999999;
}

.form__input::placeholder {
    font-style: italic;
    color: #999999;
}

.form__btn {
    margin-top: 20px;
    background-color: #f5be41;
    padding: 8px 15px;
    border: none;
    border-radius: 13px;
    font-family: inherit;
    cursor: pointer;
}

.card-container {
    position: relative;
    width: 250px;
    height: 180px;
}

@media (min-width: 480px) {
    .card-container {
        width: 400px;
        height: 230px;
    }
}

.card-container::after,
.card-container::before {
    position: absolute;
    content: '';
    overflow: hidden;
    width: 100%;
    height: 100%;
    border: 2px solid #f5be41;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 0px -3px 5px rgba(0, 0, 0, 0.25);
}

.card-container::before {
    top: -14%;
    transform: scale(0.9);
    z-index: -99;
}
.card-container::after {
    top: -28%;
    transform: scale(0.8);
    z-index: -999;
}

.card {
    position: relative;

    width: 100%;
    height: 100%;
    background: none;
    border-radius: 20px;
    transform-style: preserve-3d;
    transition: all 0.5s ease;
    box-shadow: 0px -1px 4px rgba(0, 0, 0, 0.1);
}

.card p {
    overflow: hidden;
}

.card__flip {
    transform: rotateX(180deg);
}

.card__flip-btn {
    position: absolute;
    bottom: -16px;
    left: 50%;
    transform: translateX(-50%);
    padding: 8px 20px;
    background: #fff;
    font-size: 16px;
    font-family: inherit;
    font-weight: 300;
    border: 2px solid #f5be41;
    border-radius: 10px;
    cursor: pointer;
    outline: none;

    transition: all 0.2s ease;
}

.card__front,
.card__back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    -webkit-backface-visibility: hidden;

            backface-visibility: hidden;
    overflow: hidden;
    border-radius: 20px;
    color: #000;
    font-size: 20px;
    padding: 20px;
}

.card__front {
    background-color: #fff;
    border: 2px solid #f5be41;
}

.card__back {
    background: #f5be41;
    border: 2px solid #f0f0f0;
    transform: rotateX(180deg);
}

.loader {
    display: inline-block;
    width: 80px;
    height: 80px;
}
.loader:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 10px solid #fff;
    border-color: #f5be41 transparent #f5be41 transparent;
    animation: loader 1.2s linear infinite;
}

@keyframes loader {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.completed {
    margin-bottom: 60px;
}

.buttons {
    width: 80%;
    padding-top: 20px;
    display: flex;
    justify-content: space-evenly;
}

.btn {
    font-family: inherit;
    font-weight: 400;
    font-size: 16px;
    padding: 7px 15px;
    margin: 20px;
    border: 2px solid;
    border-radius: 13px;
    background-color: transparent;
    cursor: pointer;

    color: #fff;

    transition: all 0.2s ease;
}

.btn:disabled {
    opacity: 0.6;
}

.btn--skip {
    border-color: #cf3721;
}

.btn--good {
    border-color: #258039;
}
.btn--skip:hover {
    background-color: rgb(207, 55, 33, 0.8);
}

.btn--good:hover {
    background-color: rgb(37, 128, 57, 0.8);
}

.congratulations {
    color: #258039;
    font-size: 25px;
}

.select {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid #ffdf94;
    padding: 15px;
}

.select__name {
    font-size: 18px;
    font-weight: 400;
    overflow: hidden;
    width: 60%;
}

.select__btn {
    font-family: inherit;
    font-weight: 300;
    color: #000;
    background-color: #f5be41;
    border: none;
    padding: 8px 15px;
    border-radius: 13px;
    cursor: pointer;
}

.decks__container {
    width: 100%;
}

.logo {
    margin-top: 20px;
    text-align: center;
    font-size: 44px;
    font-family: 'Fredoka One', cursive;
    color: #f5be41;
}

.navbar {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #f5be41;
    width: 280px;
    height: 60px;
    border-radius: 25px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.wrapper-nav a {
    text-decoration: none;
    color: #000;
}

.wrapper-nav a:visited {
    text-decoration: none;
    color: #000;
}

.navbar a {
    padding: 5px;
    border-radius: 10px;
}

.navbar a:hover {
    background-color: rgba(0, 0, 0, 0.15);
}

@media (min-width: 1000px) {
    .wrapper-nav {
        width: 80%;
        margin: 20px auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .logo {
        font-size: 50px;
    }

    .navbar {
        position: relative;
        left: auto;
        bottom: auto;
        right: auto;
        top: auto;
        transform: none;
        margin-top: 20px;
        width: 400px;
    }
}

.create__header {
    margin-bottom: 20px;
    font-size: 30px;
}

.add__header {
    margin-bottom: 20px;
    font-size: 30px;
}

.form__deck-select {
    border-radius: 10px;
    border-color: #f5be41;
    padding: 5px;
    outline: none;
}
.form__selection {
    font-weight: 700;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.not-found__btn {
    padding: 10px 15px;
    border: none;
    border-radius: 13px;
    margin-top: 20px;
    background-color: #f5be41;
    cursor: pointer;
}

